<template>
  <div>
    <div class="search-bar">
      <van-row>
        <van-col span="24">
          <van-search
            v-model="searcWd"
            placeholder="请输入搜索关键词"
            show-action
            @search="onSearch"
          >
            <div
              slot="action"
              @click="onSearch"
            >搜索</div>
          </van-search>
        </van-col>
      </van-row>

    </div>
    <van-cell  v-for="(video, index) in videos" :key="index" :title="video.name[0]" is-link :value="video.note[0]"  @click="goDetail(video)"/>

  </div>
</template>

<script>
import vodXml from '../utils/xml'
import { Search } from 'vant'


export default {
  data() {
    return {
      videos: [],
      searcWd: ''
    }
  },
  async created() {
    if (this.$route.params.wd) {
      this.searchByWd(this.$route.params.wd)
    }
  },
  components: { Search },
  methods: {
    onSearch() {
      this.searchByWd(this.searcWd)
    }
    , 
    async searchByWd(wd) {
      const source ={
        'kuyun':'/kuyun'
      }
      const res = await this.$axios.get(`${source.kuyun}?ac=list&t=&pg=1&h=&ids=&wd=` + wd)
      const data = await vodXml.searchXML(res.data, 'kuyun')
      this.videos = data.video
    },
    async goDetail(video){
      this.$router.push(`vod/${video.source}/${video.id[0]}`)
    }
  }

}
</script>

