<template>
  <div>
    <!-- {{this.$route.params}} -->

    <img :src="vodData.pic[0]" alt="" srcset="">
    <div> {{vodData.name[0]}} </div>
  </div>
</template>

<script>
import vodXml from '../utils/xml'

export default {
  name:'vodDetail',
  data(){
    return {
      vodData:{}
    }
  },
  async created() {
    const source = {
      'kuyun': '/kuyun'
    }
    const res = await this.$axios.get(`${source[this.$route.params.source]}?ac=videolist&t=&pg=1&h=&ids=` + this.$route.params.id)
    const data = await vodXml.getPlayLinkXml(res.data)
    console.log(data);
    this.vodData = data
  },
}
</script>

